.card{
    width:40%;
    margin: auto;
    margin-top: 120px;
    padding-bottom:10%;
}
.info{
    color:whitesmoke;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    size:50%;
}
.social{
    font-size:150%;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 170px;
    transition-duration: 500ms;

}
.borrad{
    
    border-radius: 2000px;
    width: 80%;
    height: 80%;
    transition-duration: 1000ms;
}
.borrad:hover{
    width: 85%;
    height:85%;
    transition-duration: 1000ms;
}
